@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos{
    a{
        color: v.$indigo;
    }
}

.listado-proyectos{
    list-style:none;
    padding:0;
    margin-top:3rem;
    @include m.grid(1, 2rem);
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    @include m.desktop{
        @include m.grid(3, 2rem);
    }

    .proyecto{
        border: 2px solid rgba(v.$indigo, 0.5);
        min-height: 10rem;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding:2rem;
        line-height: 2;
        border-radius: .5rem;
        &:hover{
            background-color: darken(v.$indigo, 5%);
            cursor: pointer;
            a{
                color: v.$blanco;
            }
        }

        a{
            color: v.$indigo;
            font-weight: 700;
            
        }
    }
}

.crear-proyecto{
    @include m.tablet {
        width: 65%;
    }
    width:100%;
    
}