html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  /* background: linear-gradient(to bottom, #ff4066, #FFF16A); gradiente extra*/
  /* background: linear-gradient(to right, #6157ff, #EE49FD);  gradiente extra*/
  margin: 0;
  /* Elimina el margen predeterminado del body */
  min-height: 100vh;
  /* Asegura que el fondo cubra toda la altura de la pantalla */
}

.paginas-principales {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 2rem;
  height: 80vh;
}
@media (min-width: 768px) {
  .paginas-principales {
    padding: 5rem;
  }
}

.fondo-principal {
  background: linear-gradient(to right, #2DFFF5, #fff878);
  padding-top: 8rem;
  padding-bottom: 4rem;
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

/* Agrega un fondo translúcido al formulario */
.formulario {
  padding: 20px;
  /* Ajusta el relleno según sea necesario */
  border-radius: 20px;
  /* Añade esquinas redondeadas para un aspecto más suave */
}

.formulario input {
  border: none;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.05);
  /* Fondo translúcido blanco para los campos de entrada */
}

.formulario input:hover {
  background-color: rgba(255, 255, 255, 0.1);
  /* Cambia el color de fondo al hacer hover */
  border: none;
  padding: 1rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-sm {
    width: 85%;
  }
}
@media (min-width: 1024px) {
  .contenedor-sm {
    width: 70%;
  }
}

.uptask {
  background: linear-gradient(to right, #2DFFF5, #fff878);
  color: transparent;
  background-clip: text;
}

.formulario-registrar {
  background: linear-gradient(to left, #2DFFF5, #fff878);
}

.navbar-nav .nav-link {
  transition: font-weight 0.3s ease-in-out;
  /* Agregar transición a font-weight */
}

.navbar-nav .nav-link:hover {
  font-weight: bold;
  /* Ajustar el grosor del texto en hover */
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 950;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  max-width: 80rem;
}
.alerta.errores {
  background-color: #ffc3c3;
  border-left: 0.6rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #92ff92;
  border-left: 0.6rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .boton-formulario-modal {
  background: #EE49FD;
  transition: background-color 0.3s ease;
}
.dashboard .boton-formulario-modal:hover {
  background-color: #c63bd3;
  /* Color rosa más oscuro */
}
.dashboard .boton-formulario {
  background-color: #e549e8;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 1.3rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 6px;
}
@media (min-width: 768px) {
  .dashboard .boton-formulario {
    width: auto;
  }
}
.dashboard .boton-formulario:hover {
  background-color: #de1ce2;
  cursor: pointer;
}

.sidebar {
  opacity: 0;
  background: linear-gradient(to bottom, #6157ff, #EE49FD, #6157ff);
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  display: block;
  padding: 2rem;
  height: 100%;
  opacity: 0.95;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (max-width: 768px) {
  .sidebar {
    display: none;
  }
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h1 {
  font-size: 3rem;
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 480px) {
  .sidebar .contenedor-sidebar h1 {
    font-size: 6rem;
  }
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 3.4rem;
  width: 3.4rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 4rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.2rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #32299b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 8rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #4338CA;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 1.3rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 6px;
  padding: 1.5rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #352ca3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

.barra-mobile {
  background: linear-gradient(to bottom, #6157ff, #EE49FD, #6157ff);
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
  font-size: 2.5rem;
}

.menu img {
  width: 0.8rem;
}
.menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 768px) {
  .barra .cerrar-sesion {
    max-width: 10rem;
  }
}
.barra p {
  margin: 0;
  font-size: 1.4rem;
}
.barra p span {
  font-weight: 700;
  color: #4338CA;
}
.barra .cerrar-sesion {
  background-color: #5348ff;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 1.3rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 6px;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #2315ff;
  cursor: pointer;
}

.no-proyectos a {
  color: #4338CA;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  border: 2px solid rgba(67, 56, 202, 0.5);
  min-height: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem;
}
.listado-proyectos .proyecto:hover {
  background-color: #3b31b8;
  cursor: pointer;
}
.listado-proyectos .proyecto:hover a {
  color: #FFFFFF;
}
.listado-proyectos .proyecto a {
  color: #4338CA;
  font-weight: 700;
}

.crear-proyecto {
  width: 100%;
}
@media (min-width: 768px) {
  .crear-proyecto {
    width: 65%;
  }
}

.listado-tareas {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 2rem;
  border-bottom: 2.5px solid #e4e7eb;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 1.4rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 590;
  border-radius: 14px;
}
.tarea .opciones button:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .filtros .filtros-inputs {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.filtros h3 {
  color: #4338CA;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .filtros h3 {
    margin: 0;
  }
}
.filtros .campo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 768px) {
  .filtros .campo {
    margin-bottom: 2rem;
  }
}
.filtros input[type=radio] {
  accent-color: #4338CA;
  width: 1.6rem;
  height: 1.6rem;
}